<template>
	<view class="container">
		<view class="header-top">
			<image
				class="header-bg"
				src="/static/imgs/customization/top_bg.png"
			></image>
			<view
				class="custom-top"
				:style="{
					height: menuBtnInfo.height + 'px',
					'padding-top': menuBtnInfo.top + 'px',
				}"
			></view>
			<view class="custom-header flex-column-center">
				<image
					v-if="titleImgList && titleImgList.length"
					mode="widthFix"
					:src="apiHost + '/file/' + titleImgList[0]['backgroundImg']"
				></image>
			</view>
		</view>

		<!-- 定制轮播图 -->
		<view class="custom-swiper-box">
			<view class="series-main">
				<view
					class="series-content"
					v-for="(item, index) in seriesList"
					:key="index"
				>
					<uni-swiper-dot
						:info="item.dataStr"
						:current="item.current"
						mode="dot"
						:dotsStyles="dotsStyles"
					>
						<swiper
							class="swiper-block"
							:autoplay="false"
							:interval="3000"
							:duration="1000"
							circular="true"
							previous-margin="100rpx"
							next-margin="100rpx"
							:current="item.current"
							@change="handleSeriesChange($event, index)"
						>
							<swiper-item
								class="swiper-item"
								v-for="(item1, index1) in item.dataStr"
								:key="index1"
								@click="goSearchResult(item1.jumpUrl)"
							>
								<image
									:src="apiHost + '/file/' + item1.imgUrl"
									:class="[
										'slide-image',
										item.current === index1 ? 'active' : '',
									]"
								></image>
							</swiper-item>
						</swiper>
					</uni-swiper-dot>
				</view>
			</view>
		</view>

		<!-- 分类 -->
		<view class="classify-box">
			<image
				v-for="(item, index) in goodsList"
				:key="index"
				@click="goSearchResult(item.imgUrl)"
				class="classify-main-img"
				:src="apiHost + '/file/' + item.backgroundImg"
			></image>
		</view>

		<!-- 设计师 -->
		<view class="design-box">
			<view class="design-head">
				<image
					v-if="titleImgList && titleImgList.length"
					class="head-img"
					:src="apiHost + '/file/' + titleImgList[1]['backgroundImg']"
				></image>
			</view>
			<view class="list">
				<view
					class="item"
					v-for="(item, index) in designerList"
					:key="index"
					@click="goDesginerDetail(item)"
				>
					<EasyLoadimage
						class="design-cover-img"
						:imageSrc="apiHost + '/file/' + item.imgUrl"
						:scrollTop="scrollTop"
					/>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
import * as Api from '@/api/api';
import { api_host } from '@/utils/request';
import EasyLoadimage from '@/components/easy-loadimage/easy-loadimage';
export default {
	components: {
		EasyLoadimage,
	},
	name: 'customization',
	data() {
		return {
			apiHost: api_host,
			menuBtnInfo: uni.getMenuButtonBoundingClientRect(),
			designList: [],
			scrollTop: 0,
			dotsStyles: {
				width: 15,
				height: 2,
				backgroundColor: '#CACED4',
				border: '#CACED4',
				selectedBackgroundColor: '#171D26',
				selectedBorder: '#171D26',
			},
			seriesList: [], // 轮播定制列表
			titleImgList: [], // 标题图数据
			goodsList: [], // 产品列表
			designerList: [], // 设计师列表
		};
	},
	onLoad() {
		this.indexOrSortDataGet();
	},
	methods: {
		async indexOrSortDataGet() {
			const { data: res } = await Api.indexOrSortDataGet(2);
			if (res.code == 200) {
				if (res.data && res.data.length) {
					console.log(res.data);
					// 系列数据
					let seriesList = res.data
						.filter((item) => {
							return (
								item.viewType == 1 && item.relationType == 2 && item.dataStr
							);
						})
						.map((item1) => {
							item1.dataStr = JSON.parse(item1.dataStr);
							item1.current = 0;
							return item1;
						});

					// 整体图+路径(列表) imgUrl backgroundImage viewType 0
					let goodsList = res.data.filter((item) => {
						return (
							item.viewType == 0 &&
							item.relationType == 2 &&
							item.imgUrl &&
							item.backgroundImg
						);
					});
					// 标题图数据 imgUrl为空 backgroundImage viewType 0 elationType 2
					let titleImgList = res.data.filter((item) => {
						return (
							item.viewType == 0 &&
							item.relationType == 2 &&
							!item.imgUrl &&
							item.backgroundImg
						);
					});
					this.titleImgList = titleImgList;

					// 设计师数据
					let findDesigner = res.data.find((item) => {
						return item.viewType == 1 && item.relationType == 3 && item.dataStr;
					});
					if (findDesigner && findDesigner.dataStr) {
						let designList = JSON.parse(findDesigner.dataStr);
						this.designerList = designList;
					}

					this.seriesList = seriesList;
					this.goodsList = goodsList;
					console.log('系列数据', this.seriesList);
					console.log('列表', this.goodsList);
					console.log('标题图数据', this.titleImgList);
					console.log('设计师列表', this.designerList);
				}
			}

			setTimeout(() => {
				this.scrollTop++;
			}, 500);
		},
		// 系列轮播滑动
		handleSeriesChange(event, index) {
			let current = event.detail.current;
			this.seriesList[index]['current'] = current;
		},
		goSearchResult(url) {
			console.log(url);
			uni.navigateTo({
				url: url,
			});
		},
		// 设计师详情
		goDesginerDetail(item) {
			console.log(item);
			let url = `${item.jumpUrl}&designerImg=${encodeURIComponent(
				JSON.stringify(item.designerImg)
			)}`;

			if (item.jumpUrl) {
				uni.navigateTo({
					url: url,
				});
			}
		},
	},
	onPageScroll(e) {
		this.scrollTop = e.scrollTop;
	},
};
</script>

<style lang="scss" scoped>
.container {
	width: 100%;
	padding-bottom: 20rpx;
	font-family: 'PingFang SC-Medium';

	.header-top {
		position: relative;

		.header-bg {
			width: 100%;
			height: 658rpx;
			position: absolute;
			top: 0;
			left: 0;
			z-index: -1;
		}

		.custom-header {
			width: 100%;
			position: relative;
			.logo-icon {
				width: 690rpx;
				height: 140rpx;
			}
		}
	}

	// 轮播图
	.custom-swiper-box {
		margin-top: 80rpx;
		.series-content {
			position: relative;
			z-index: 9;
			.swiper-block {
				height: 836rpx;
				width: 100%;
			}

			.swiper-item {
				display: flex;
				flex-direction: column;
				align-items: center;
				overflow: unset;
				position: relative;
				box-sizing: border-box;
				margin-top: 72rpx;
			}

			/deep/ .uni-swiper__dots-box {
				bottom: 20px !important;
				display: none !important;
			}

			.slide-image {
				z-index: 1;
				width: 480rpx;
				height: 676rpx;
			}

			.active {
				transform: scale(1.14);
				transition: all 0.2s ease-in 0s;
				z-index: 20;
			}
		}
	}

	.classify-box {
		display: flex;
		flex-direction: column;
		width: 100%;
		padding: 0 30rpx;
		box-sizing: border-box;
		margin-top: 80rpx;
		image {
			width: 100%;
			height: 1080rpx;
			margin-bottom: 48rpx;
		}
	}

	.design-box {
		.design-head {
			image {
				width: 100%;
				height: 312rpx;
			}
		}
		.list {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			width: 100%;
			padding: 0 30rpx;
			box-sizing: border-box;
			margin-top: 20rpx;
			.item {
				width: 330rpx;
				height: 564rpx;
				margin-bottom: 40rpx;
				image {
					width: 100%;
					height: 100%;
				}
			}
		}
	}
}
</style>
